<template>

  <el-container class="layout-page">
    <el-aside width="200px">
      <div class="logo">面经运营后台</div>
      <el-menu
      router
      background-color="#313a46"
      text-color="#8391a2"
      active-text-color="#FFF"
      :default-active="$route.path">
        <el-menu-item index="/dashboard">

          <i class="el-icon-pie-chart"></i>
          <span>数据看板</span>
        </el-menu-item>
        <el-menu-item index="/article">
          <i class="el-icon-notebook-1"></i>
          <span>面经管理</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header>  <div class="user">
          <el-avatar
            :size="36"
            :src="avatar"
          ></el-avatar>
          <el-link :underline="false">{{username}} </el-link>
        </div>
       <div class="logout">
          <el-popconfirm title="您确认退出面经运营后台吗？" @confirm="logout">
            <i slot="reference" title="退出登录" class="el-icon-switch-button"></i>
          </el-popconfirm>
        </div>
      </el-header>
      <el-main>
        <!-- 配置路由 出口 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
// import { delToken } from '@/utils/storage'
import { getUserInfo } from '@/api/user'
import { mapMutations } from 'vuex'
export default {
  name: 'layout-index',
  data () {
    return {
      avatar: '',
      username: ''
    }
  },
  async created () {
    try {
      const res = await getUserInfo()
      this.avatar = res.data.avatar
      this.username = res.data.username
    } catch (error) {
      this.$message.error(error.response.data.message)
    }
  },
  methods: {
    ...mapMutations('user', ['removeToken']),
    logout () {
      // delToken()//方法一
      // 退出 删除token 跳转到登录
      // 1.vuex 里面的 token 要删除 2.本地存储里面的 token 要删除
      this.removeToken()
      // this.$router.push('/login')
      // 在退出的时候 记录 当前 url 上面的路由地址
      // console.log(this.$route.fullPath);
      this.$router.push(`/login?redirect=${this.$route.fullPath}`)
    }
  }

}
</script>

<style lang="scss" scoped>
.layout-page {
  height: 100vh;

  .el-aside {
    background: #313a46;

    .logo {
      color: #fff;
      font-size: 20px;
      height: 60px;
      line-height: 60px;
      text-align: center;
    }

    .el-menu {
      border-right: none;
      margin-top: 20px;

      &-item {
        background-color: transparent !important;

        >span,
        i {
          padding-left: 5px;
        }
      }
    }
  }

  .el-header {
    box-shadow: 0px 0px 35px 0px rgba(154, 161, 171, 0.15);
    background: #fff;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    z-index: 999;

    .user {
      display: flex;
      align-items: center;
      background: #fafbfd;
      height: 60px;
      border: 1px solid #f1f3fa;
      padding: 0 15px;

      .el-avatar {
        margin-right: 15px;
      }
    }

    .logout {
      font-size: 20px;
      color: #999;
      cursor: pointer;
      padding: 0 15px;
    }
  }

  // .el-footer {
  //   display: flex;
  //   justify-content: space-between;
  //   align-items: center;
  //   color: #aaa;
  //   border-top: 1px solid rgba(152, 166, 173, 0.2);
  //   font-size: 14px;
  // }
}
</style>
